import './App.css';
import React, { useEffect, useState } from 'react';
import _ from 'underscore';
import { Container, Row, Col, Form } from 'react-bootstrap';
import { Bar, BarChart, ResponsiveContainer, LineChart, Label, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

function App() {
  return (
    <Container fluid>
      <header className="jumbotron py-2 my-3">
        <h1 className="text-center">Singapore Government Data Visualiser</h1>
        <h5 className="text-center">Just displaying some Singapore Governemnt data from data.gov.sg</h5>
      </header>
      <GovDataChart />
    </Container >

  );
}



function ChartSettings(props) {

  let [xMin, setXMin, xMax, setXMax, yMin, setYMin, yMax, setYMax] = props.domain;
  let [resourceID, setResourceID] = props.resourceID;
  let [xKey, setXKey, yKey, setYKey, series, setSeries] = props.keys;
  let [sumData, setSumData] = props.sumData;
  let fields = props.fields ? Object.values(props.fields).splice(1).map(item => item.id) : ['Loading...'];

  return (
    <Form>
      <Row><h2 className="mb-3">Chart Settings</h2></Row>
      <Form.Group className="mb-3" controlId="dataset">
        <Row><h3 className="mb-3">Dataset</h3></Row>
        <Row>
          <Col xs={4}><Form.Label>Resource ID</Form.Label></Col>
          <Col><Form.Control type="text" defaultValue={resourceID} onChange={(e) => setResourceID(e.target.value)} /></Col>
        </Row>
        <Row>
          <Form.Text>Valid inputs: a data.gov.sg resource_id, e.g. 'd4dbffc1-b097-4d86-9da8-afaf91dfbcbd'</Form.Text>
        </Row>
      </Form.Group>
      <Form.Group className="mb-3" controlId="dataset">
        <Row><h3 className="mb-3">Axis Labels</h3></Row>
        <Row>
          <Col xs={4}><Form.Label>X Axis Label</Form.Label></Col>
          <Col><Form.Select value={xKey} onChange={(e) => setXKey(e.target.value)}>
            {fields.map(key => (
              <option value={key}>{key}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={4}><Form.Label>Y Axis Label</Form.Label></Col>
          <Col><Form.Select value={yKey} onChange={(e) => setYKey(e.target.value)}>
            {fields.map(key => (
              <option value={key}>{key}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={4}><Form.Label>Series Label</Form.Label></Col>
          <Col><Form.Select value={series} onChange={(e) => setSeries(e.target.value)}>
            {fields.map(key => (
              <option value={key}>{key}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={6}><Form.Label>Sum Duplicate Data?</Form.Label></Col>
          <Col><Form.Check type="checkbox" checked={sumData} onChange={(e) => setSumData(!sumData)} /></Col>
        </Row>
        <Row>
          <Form.Text>Correspnding dataset column name, eg. 'financial_year', 'amount'</Form.Text>
        </Row>
      </Form.Group>
      <Form.Group className="mb-3" controlId="xyAxisDomain">
        <Row><h3 className="mb-3">Chart Range</h3></Row>
        <Row>
          <Col><Form.Text>Min X Value</Form.Text> </Col>
          <Col><Form.Control type="text" defaultValue={xMin} onChange={(e) => setXMin(e.target.value)} /></Col>
          <Col><Form.Text>Max X Value</Form.Text> </Col>
          <Col><Form.Control type="text" defaultValue={xMax} onChange={(e) => setXMax(e.target.value)} /></Col>
        </Row>
        <Row>
          <Col><Form.Text>Min Y Value</Form.Text></Col>
          <Col><Form.Control type="text" defaultValue={yMin} onChange={(e) => setYMin(e.target.value)} /></Col>
          <Col><Form.Text>Max Y Value</Form.Text></Col>
          <Col><Form.Control type="text" defaultValue={yMax} onChange={(e) => setYMax(e.target.value)} /></Col>
        </Row>
        <Row>
          <Form.Text>Valid inputs: a number, 'auto', 'dataMin', 'dataMax', or a string like 'dataMin - 20', 'dataMax + 100'</Form.Text>
        </Row>
      </Form.Group>
    </Form >
  );
}



function SimpleLineChart(props) {

  let [xMin, xMax, yMin, yMax] = props.domain;

  function parseIntOrText(value) {
    let int = parseInt(value);
    return isNaN(int) ? value : int;
  }

  function getRandomDarkColor() {
    let color = "#";
    for (let i = 0; i < 6; i++) {
      color += Math.floor(Math.random() * 10);
    }
    return color;
  }

  if (props.error) return <div>Error: {props.error.message}</div>;
  if (!props.isLoaded) return <div>Loading Data...</div>;

  return (
    <ResponsiveContainer width="100%" height={600}>
      <LineChart>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis type="number" dataKey={props.xKey} domain={[parseIntOrText(xMin), parseIntOrText(xMax)]}>
          <Label value={props.xKey} offset={0} position="insideBottom" />
        </XAxis>
        <YAxis interval="preserveStartEnd" allowDataOverflow={true} domain={[parseIntOrText(yMin), parseIntOrText(yMax)]}>
          <Label value={props.yKey} angle={-90} offset={0} position="insideLeft" />
        </YAxis>
        <Tooltip filterNull="true" itemStyle={{ fontSize: '0.7em', padding: 0 }} />
        <Legend wrapperStyle={{ fontSize: '0.7em' }} />
        {Object.keys(props.dataset).map(key => (
          <Line type="natural" legendType="square" activeDot={{ r: 6 }} dataKey={props.yKey} data={props.dataset[key]} name={key} key={key} stroke={getRandomDarkColor()} />
        ))}
      </LineChart>
    </ResponsiveContainer>
  );

}



function SimpleBarChart(props) {
  console.log(Object.values(props.dataset)[0]);

  if (props.error) return <div>Error: {props.error.message}</div>;
  if (!props.isLoaded) return <div>Loading Data...</div>;

  return (
    <BarChart>
      <Bar data={Object.values(props.dataset)[0]} dataKey="amount" />
    </BarChart>
  );
}



function GovDataChart(props) {
  const [resourceID, setResourceID] = useState('d4dbffc1-b097-4d86-9da8-afaf91dfbcbd');
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [result, setResult] = useState([]);
  const [sumData, setSumData] = useState(false);
  const [xMin, setXMin] = useState('auto');
  const [xMax, setXMax] = useState('auto');
  const [yMin, setYMin] = useState('0');
  const [yMax, setYMax] = useState('auto');
  const [xKey, setXKey] = useState('financial_year');
  const [yKey, setYKey] = useState('amount');
  const [series, setSeries] = useState('type');
  const [dataset, setDataset] = useState([]);

  useEffect(() => {
    setIsLoaded(false);
    let url = "https://data.gov.sg/api/action/datastore_search?limit=1000";
    url += "&resource_id=" + resourceID;
    console.log(url);
    fetch(url)
      .then(res => res.json())
      .then(
        (res) => {
          if (!res.success) setError("API error")
          else setResult(res.result);
          setIsLoaded(true);
        },
        (error) => {
          setError(error);
          setIsLoaded(true);
        }
      )
  }, [resourceID]);

  function parseIntOrText(value) {
    let int = parseInt(value);
    return isNaN(int) ? value : int;
  }

  useEffect(() => {
    if (!sumData) {
      setDataset(_.groupBy(result.records, series));
      return;
    }
    let set = {};
    for (const i in result.records) {
      let item = result.records[i];
      let seriesID = item[series];
      if (!set[seriesID]) {
        // series doesnt exist yet, insert first data point
        set[seriesID] = [{ [xKey]: item[xKey], [yKey]: parseIntOrText(item[yKey]) }];
        continue;
      }
      // series exists,  check if xKey exists in series
      let found = false;
      for (const j in set[seriesID]) {
        if (set[seriesID][j][xKey] === item[xKey]) {
          // x-key exists, lets add y Value
          set[seriesID][j][yKey] += parseIntOrText(item[yKey]);
          found = true;
          break;
        }
      }
      if (!found) set[seriesID].push({ [xKey]: item[xKey], [yKey]: parseIntOrText(item[yKey]) });

    }
    setDataset(set);
  }, [sumData, result, series, xKey, yKey]);

  return (
    <Row className="justify-items-center">
      <Col xs={12} md={3}>
        <ChartSettings
          keys={[xKey, setXKey, yKey, setYKey, series, setSeries]}
          domain={[xMin, setXMin, xMax, setXMax, yMin, setYMin, yMax, setYMax]}
          resourceID={[resourceID, setResourceID]}
          fields={result.fields}
          sumData={[sumData, setSumData]}
        />
      </Col>
      <Col xs={12} md={9}>
        <Container className="justify-items-center">
          <h3>{props.title}</h3>
          <SimpleLineChart isLoaded={isLoaded} error={error} dataset={dataset} xKey={xKey} yKey={yKey} domain={[xMin, xMax, yMin, yMax]} />
        </Container>
      </Col>
    </Row>
  );
}




export default App;
